import { Component, Fragment } from 'react';
import axios from 'axios';
import { Spinner } from "react-loading-io";
import { authService, firebaseInstance } from "../public/firebaseConfig";
import { CopyToClipboard } from 'react-copy-to-clipboard'
import * as config from '../config';
import reseticon from '../public/reset.png';
import trashicon from '../public/trash.jpg';
import copyicon from '../public/copy.png';
  import { ToastContainer, toast } from 'react-toastify';
  import 'react-toastify/dist/ReactToastify.css';
import '../style/Main.css';


class Main extends Component {
  constructor() {
    super();
    this.state = { 
      input: '',
      output: '',
      outputBeforeTlanslate: '',
      outputAfterTlanslate: '',
      loading: false,
      isFollow: false,
      options: ['ÌåêÌÉÄÏßÄ','ÌòÑÌåê','Î¨¥Ìòë','ÎØ∏Ïä§ÌÑ∞Î¶¨','Î°úÌåê'],
      selectOptions: 'ÌåêÌÉÄÏßÄ',
      Main_character: '',
      Place: '',
      Time: '',
      Main_Events: '',
      Material: '',
      
      before_selectOptions: 'ÌåêÌÉÄÏßÄ',
      before_Main_character: '',
      before_Place: '',
      before_Time: '',
      before_Main_Events: '',
      before_Material: '',
      before_outputBeforeTlanslate: '',
      before_isFollow: false
    };

    this.handleChange = this.handleChange.bind(this);
    this.requestcontents = this.requestcontents.bind(this);
    this.onSelect = this.onSelect.bind(this);
    this.onSelectFollow = this.onSelectFollow.bind(this);
    this.resetData = this.resetData.bind(this);
  }

  handleChange(e){
    if(e.target.value.length < 20 && e.target.name === 'Main_Events'){   
      this.setState({[e.target.name]: e.target.value});
    } else if(e.target.value.length < 10){
      this.setState({[e.target.name]: e.target.value});
    }else{
      toast.error(`ü¶Ñ ${e.target.value.length}Í∏ÄÏûêÎ•º ÎÑòÏñ¥Í∞à Ïàò ÏóÜÏñ¥Ïöî!`, {
        position: "top-right",
        autoClose: 3000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      });
    }
  }

  async onSelectFollow(e){
    console.log(e.target.value);
    await this.setState({ isFollow: e.target.value});
    console.log(this.state.isFollow);

  }

  onSelect(e){
    this.setState({ selectOptions: e.target.value});
  }

  async requestcontents(e){
    await this.refreshProfile();
    if (localStorage.getItem('token') !== undefined) {
      this.setState({loading: true}); 
      let story = '';
      let selectOptions = this.state.selectOptions;
      let Main_character = this.state.Main_character;
      let Place = this.state.Place;
      let Time = this.state.Time;
      let Main_Events = this.state.Main_Events;
      let Material = this.state.Material;
      let isFollow = this.state.isFollow;

      if (e.target.name == 'reset') {
        console.log('reset');
        selectOptions = this.state.before_selectOptions;
        Main_character = this.state.before_Main_character;
        Place = this.state.before_Place;
        Time = this.state.before_Time;
        Main_Events = this.state.before_Main_Events;
        Material = this.state.before_Material;
        story = this.state.before_outputBeforeTlanslate;
        isFollow = this.state.before_isFollow;
        await this.setState({selectOptions: selectOptions});
        await this.setState({Main_character: Main_character});
        await this.setState({Place: Place});
        await this.setState({Time: Time});
        await this.setState({Main_Events: Main_Events});
        await this.setState({Material: Material});
        await this.setState({outputBeforeTlanslate: story});
        await this.setState({isFollow: isFollow});
      }

      if (isFollow == 'true') { 
        console.log('isFollow is true');
        story = this.state.outputBeforeTlanslate; 
        console.log(story);
      }else{
        await this.setState({ outputAfterTlanslate: ''});
        await this.setState({ outputBeforeTlanslate: ''});
        await this.setState({ output: ''});
      }

      if (Main_character == '') {
        toast.error(`Ï£ºÏù∏Í≥µÏùÑ ÏûÖÎ†•Ìï¥ Ï£ºÏÑ∏Ïöî!`, {
          position: "top-right",
          autoClose: 3000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
        });
        return;
      }else if(Place == ''){
          toast.error(`Ïû•ÏÜåÎ•º ÏûÖÎ†•Ìï¥ Ï£ºÏÑ∏Ïöî!`, {
          position: "top-right",
          autoClose: 3000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
        });
        return;
      }else if(Time == ''){
          toast.error(`ÏãúÍ∞ÑÎåÄÎ•º ÏûÖÎ†•Ìï¥ Ï£ºÏÑ∏Ïöî!`, {
          position: "top-right",
          autoClose: 3000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
        });
        return;
      }else if(Main_Events == ''){
          toast.error(`Ï£ºÏöî ÏÇ¨Í±¥ÏùÑ ÏûÖÎ†•Ìï¥ Ï£ºÏÑ∏Ïöî!`, {
          position: "top-right",
          autoClose: 3000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
        });
        return;
      }else if(Material == ''){
          toast.error(`ÏÜåÏû¨Î•º ÏûÖÎ†•Ìï¥ Ï£ºÏÑ∏Ïöî!`, {
          position: "top-right",
          autoClose: 3000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
          progress: undefined,
        });
        return;
      }

      axios.post(`${config.SERVER_URL}/complation`,
        { selectOptions: selectOptions,
          Main_character: Main_character,
          Place: Place,
          Time: Time,
          Main_Events: Main_Events,
          Material: Material,
          Story: story }, 
        { headers: {authentication: localStorage.getItem('token')},
        timeout: 100000 })
      .then((response) => {
        console.log(response.data);
        this.setState({ outputAfterTlanslate: this.state.outputAfterTlanslate + response.data[0]});
        this.setState({ outputBeforeTlanslate: this.state.outputBeforeTlanslate + response.data[1]});
        this.setState({ output: this.state.outputAfterTlanslate+ '\n\nÏõêÎ≥∏\n'+ this.state.outputBeforeTlanslate});
        this.setState({loading: false});
        console.log(response.data.warn);
        if(response.data[2] >= 3){
          toast.error(`Ìè≠Î†•Ï†ÅÏù¥Í±∞ÎÇò ÏÑ†Ï†ïÏ†ÅÏù∏ Í≤∞Í≥ºÍ∞íÏù¥ ÎèÑÏ∂úÎêòÏóàÏùÑ Ïàò ÏûàÏñ¥Ïöî!`, {
            position: "top-right",
            autoClose: 4000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
          });
        }

        this.setState({before_selectOptions: selectOptions});
        this.setState({before_Main_character: Main_character});
        this.setState({before_Place: Place});
        this.setState({before_Time: Time});
        this.setState({before_Main_Events: Main_Events});
        this.setState({before_Material: Material});
        this.setState({before_outputBeforeTlanslate: story});
        this.setState({before_isFollow: isFollow});

 
      }).catch((error) => {
        console.log(error);
        if (error.response.status === 412) {
          this.setState({loading: false});
          this.setState({ output: 'Ïû¨Î°úÍ∑∏Ïù∏Ïù¥ ÌïÑÏöîÌï©ÎãàÎã§!'});
          localStorage.removeItem('token');
        }else{
          this.setState({loading: false});
          this.setState({ output: 'ÌÜ†ÌÅ∞Ïù¥ Î∂ÄÏ°±Ìï©ÎãàÎã§!'});
        }
      })
    }else{
      this.setState({output: 'Î°úÍ∑∏Ïù∏ ÌõÑ Îã§Ïãú ÏãúÎèÑÌï¥ Ï£ºÏÑ∏Ïöî!'});
    }
  }


  resetData(){
    this.setState({next: ''});
    this.setState({input: ''});
    this.setState({output: ''});
    this.setState({outputBeforeTlanslate: ''});
    this.setState({outputAfterTlanslate: ''});
    this.setState({Main_character: ''});
    this.setState({Place: ''});
    this.setState({Time: ''});
    this.setState({Main_Events: ''});
    this.setState({Material: ''});
  }

  async refreshProfile() {
    authService.onAuthStateChanged(async (user) => {
      if (authService.currentUser) {
        authService.currentUser.getIdToken().then(async (data) => {
          await localStorage.setItem('token', data)
        }).catch(async (error) => {
          console.log(error);
        });
      }
    });
  }

  async copyContents() {
    document.execCommand("copy");
  }
  render() {
    return (
      <Fragment>

      <div class="sub">
        <div class="input">
          <select className='dropdowncategory' onChange={this.onSelect} >
            <option value={this.state.options[0]}>{this.state.options[0]}</option>
            <option value={this.state.options[1]}>{this.state.options[1]}</option>
            <option value={this.state.options[2]}>{this.state.options[2]}</option>
            <option value={this.state.options[3]}>{this.state.options[3]}</option>
            <option value={this.state.options[4]}>{this.state.options[4]}</option>
        </select>
        </div>
        <input class="sub_input_text" value={this.state.Main_character} onChange={this.handleChange} name='Main_character' placeholder="Ï£ºÏöî Ïù∏Î¨º"></input>
        <input class="sub_input_text" value={this.state.Place} onChange={this.handleChange} name='Place' placeholder="Ïû•ÏÜå"></input>
        <input class="sub_input_text" value={this.state.Time} onChange={this.handleChange} name='Time' placeholder="ÏãúÍ∞Ñ"></input>
        <input class="sub_input_text" value={this.state.Main_Events} onChange={this.handleChange} name='Main_Events' placeholder="Ï£ºÏöî ÏÇ¨Í±¥"></input>
        <input class="sub_input_text" value={this.state.Material} onChange={this.handleChange} name='Material' placeholder="ÏÜåÏû¨"></input>
        <button class="start" onClick = {this.requestcontents}>Start!</button>
        <select className='followSelector' onChange={this.onSelectFollow}>
          <option value={false}>ÎèÑÏûÖÎ∂Ä Ïì∞Í∏∞</option>
          <option value={true}>Ï§ÑÍ±∞Î¶¨ Ïì∞Í∏∞</option>
        </select>
      </div>

      <div class="main">
        <div class="landing">
          AIÍ∞Ä ÎãπÏã†Ïùò Ïù¥ÏïºÍ∏∞Ïóê ÌïÑÏöîÌïú ÏòÅÍ∞êÏùÑ Ï†úÍ≥µÌï©ÎãàÎã§.
        </div>
        {this.state.loading ? 
        <div class="loading"> <Spinner size='8px' color='#3b2479'/> </div> : null}
        <div>
          <div>
            <textarea class="output" value={this.state.output} readOnly></textarea>
            <div class="reset">
              <a onClick={this.resetData}>
                <img  src={trashicon} class="reseticon"/>
              </a>
            </div>
            <div class="reset">
            <CopyToClipboard text={this.state.output}>
              <a>
                <img  src={copyicon} class="reseticon"/>
              </a>
            </CopyToClipboard>
            </div>
            <div class="reset">
              <a onClick={this.requestcontents}>
                <img  src={reseticon} class="reseticon"  name="reset"/>
              </a>
            </div>
          </div>
        </div>
      </div>

      <ToastContainer
        position="top-right"
        autoClose={3000}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss={false}
        draggable
        pauseOnHover
      />
      </Fragment>
    );
  }
}

export default Main;
